<script>
import BaseCard from '../../../components/BaseCard.vue'
import BaseButton from '../../../components/BaseButton.vue'
import LoadingAnimation from '../../../components/LoadingAnimation.vue'
import dayjs from 'dayjs'

export default {
  name: 'AdminQuoteDetail',
  components: {
    BaseCard,
    BaseButton,
    LoadingAnimation
  },
  data() {
    return {
      quoteId: '',
      productId: '',
      quote: {
        user_id: '',
        user_name: '',
        product_id: '',
        product_name: '',
        status: 'pending',
        forwarder_name: '',
        shipping_route: '',
        delivery_time: '',
        price_per_kg: 0,
        total_gross_weight: 0,
        total_freight: 0,
        customs_fee: 0,
        total_cost: 0,
        remark: '',
        created_at: new Date(),
        updated_at: null,
        reviewer: ''
      },
      isLoading: true,
      hasError: false,
      errorMessage: '',
      statusIcons: {
        pending: '‚è≥',
        approved: '‚úÖ',
        rejected: '‚ùå',
        expired: '‚è±Ô∏è',
        cancelled: 'üö´'
      },
      statusMessages: {
        pending: {
          title: 'ÂæÖÂÆ°Ê†∏',
          desc: 'ËØ•Êä•‰ª∑Ê≠£Âú®Á≠âÂæÖÂÆ°Ê†∏ÔºåÊÇ®ÂèØ‰ª•ÈÄâÊã©ÈÄöËøáÊàñÊãíÁªù„ÄÇ'
        },
        approved: {
          title: 'Â∑≤ÈÄöËøá',
          desc: 'ÊÇ®Â∑≤ÁªèÈÄöËøá‰∫ÜËØ•Êä•‰ª∑ÔºåÁî®Êà∑ÂèØ‰ª•Êü•ÁúãÂπ∂ÁªßÁª≠ÂêéÁª≠ÊµÅÁ®ã„ÄÇ'
        },
        rejected: {
          title: 'Â∑≤ÊãíÁªù',
          desc: 'ÊÇ®Â∑≤ÊãíÁªù‰∫ÜËØ•Êä•‰ª∑ÔºåÁî®Êà∑ÂèØ‰ª•Êü•ÁúãÊãíÁªùÂéüÂõ†Âπ∂ÈáçÊñ∞Êèê‰∫§„ÄÇ'
        },
        expired: {
          title: 'Â∑≤ËøáÊúü',
          desc: 'ËØ•Êä•‰ª∑Â∑≤ËøáÊúüÔºå‰∏çÈúÄË¶ÅËøõ‰∏ÄÊ≠•Êìç‰Ωú„ÄÇ'
        },
        cancelled: {
          title: 'Â∑≤ÂèñÊ∂à',
          desc: 'Áî®Êà∑Â∑≤ÂèñÊ∂à‰∫ÜËØ•Êä•‰ª∑Ôºå‰∏çÈúÄË¶ÅËøõ‰∏ÄÊ≠•Êìç‰Ωú„ÄÇ'
        }
      },
      shouldFail: false // Áî®‰∫éÂºÄÂèëÊó∂Ê®°ÊãüËØ∑Ê±ÇÂ§±Ë¥•ÁöÑÂºÄÂÖ≥
    }
  },
  onLoad(options) {
    this.quoteId = options.quoteId || ''
    this.productId = options.productId || ''
    
    // ÂºÄÂèëÁéØÂ¢É‰∏ãÈöèÊú∫ÂÜ≥ÂÆöÊòØÂê¶Ê®°ÊãüÂ§±Ë¥• (10%ÁöÑÊ¶ÇÁéáÂ§±Ë¥•)
    if (process.env.NODE_ENV === 'development') {
      this.shouldFail = Math.random() < 0.1
    }
    
    this.fetchQuoteDetail()
  },
  methods: {
    async fetchQuoteDetail() {
      this.isLoading = true
      this.hasError = false
      
      try {
        // Ê®°ÊãüAPIËØ∑Ê±ÇÔºåÂáèÂ∞ëÂä†ËΩΩÊó∂Èó¥‰ª•‰æøË∞ÉËØï
        setTimeout(() => {
          // Â¶ÇÊûúshouldFail‰∏∫trueÔºåÂàôÊ®°ÊãüËØ∑Ê±ÇÂ§±Ë¥•
          if (this.shouldFail) {
            this.handleError(new Error('Ê®°ÊãüËØ∑Ê±ÇÂ§±Ë¥•ÔºåËøôÊòØ‰∏Ä‰∏™ÈöèÊú∫ÊµãËØïÈîôËØØ'))
            return
          }
          
          // Ê®°ÊãüÊï∞ÊçÆ
          const mockData = {
            quote_id: this.quoteId || 'Q10001',
            user_id: 'U1002',
            user_name: 'ÊùéÂõõ',
            product_id: this.productId || 'P1001',
            product_name: 'Êô∫ËÉΩÊâãË°® Pro Max',
            status: 'pending',
            forwarder_name: 'ÁéØÁêÉÂø´ÈÄí',
            shipping_route: '‰∏äÊµ∑ - Ê¥õÊùâÁü∂',
            delivery_time: '15-20Â§©',
            price_per_kg: 45,
            total_gross_weight: 350,
            total_freight: 15750,
            customs_fee: 500,
            total_cost: 16250,
            remark: 'ÈúÄË¶ÅÁâπÊÆäÂåÖË£ÖÔºåËØ∑Âú®ËøêËæìËøáÁ®ã‰∏≠Ê≥®ÊÑèÈò≤Èúá„ÄÇ',
            created_at: new Date(Date.now() - 2 * 24 * 60 * 60 * 1000), // 2Â§©Ââç
            updated_at: null,
            reviewer: ''
          }
          
          this.quote = mockData
          this.isLoading = false
        }, 300) // Â∞ÜÊ®°ÊãüÂª∂Ëøü‰ªé800msÈôç‰ΩéÂà∞300msÔºåÊèêÈ´òÂìçÂ∫îÈÄüÂ∫¶
      } catch (error) {
        this.handleError(error)
      }
    },
    
    handleError(error) {
      console.error('Ëé∑ÂèñÊä•‰ª∑ËØ¶ÊÉÖÂ§±Ë¥•:', error)
      this.hasError = true
      this.errorMessage = error.message || 'Ëé∑ÂèñÊä•‰ª∑ËØ¶ÊÉÖÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'
      this.isLoading = false
    },
    
    formatDate(date) {
      if (!date) return '-'
      return dayjs(date).format('YYYY-MM-DD HH:mm')
    },
    
    getStatusText(status) {
      const statusMap = {
        pending: 'ÂæÖÂÆ°Ê†∏',
        approved: 'Â∑≤ÈÄöËøá',
        rejected: 'Â∑≤ÊãíÁªù',
        expired: 'Â∑≤ËøáÊúü',
        cancelled: 'Â∑≤ÂèñÊ∂à'
      }
      return statusMap[status] || status
    },
    
    handleApprove() {
      uni.showModal({
        title: 'Á°ÆËÆ§ÈÄöËøá',
        content: 'Á°ÆÂÆöË¶ÅÈÄöËøáËØ•Êä•‰ª∑ÂêóÔºüÈÄöËøáÂêéÂ∞ÜÈÄöÁü•Áî®Êà∑„ÄÇ',
        success: (res) => {
          if (res.confirm) {
            // Êõ¥Êñ∞Áä∂ÊÄÅ
            this.quote.status = 'approved'
            this.quote.updated_at = new Date()
            this.quote.reviewer = 'ÁÆ°ÁêÜÂëò'
            
            // ÊòæÁ§∫ÊàêÂäüÊèêÁ§∫
            uni.showToast({
              title: 'Â∑≤ÈÄöËøáËØ•Êä•‰ª∑',
              icon: 'success'
            })
          }
        }
      })
    },
    
    handleReject() {
      uni.showModal({
        title: 'ÊãíÁªùÊä•‰ª∑',
        content: 'Á°ÆÂÆöË¶ÅÊãíÁªùËØ•Êä•‰ª∑ÂêóÔºüÊãíÁªùÂêéÂ∞ÜÈÄöÁü•Áî®Êà∑„ÄÇ',
        success: (res) => {
          if (res.confirm) {
            // Êõ¥Êñ∞Áä∂ÊÄÅ
            this.quote.status = 'rejected'
            this.quote.updated_at = new Date()
            this.quote.reviewer = 'ÁÆ°ÁêÜÂëò'
            
            // ÊòæÁ§∫ÊàêÂäüÊèêÁ§∫
            uni.showToast({
              title: 'Â∑≤ÊãíÁªùËØ•Êä•‰ª∑',
              icon: 'success'
            })
          }
        }
      })
    },
    
    handleGoBack() {
      uni.navigateBack()
    },
    
    handleContact() {
      uni.showModal({
        title: 'ËÅîÁ≥ªÁî®Êà∑',
        content: `ÊòØÂê¶ËÅîÁ≥ªÁî®Êà∑ ${this.quote.user_name}Ôºü`,
        success: (res) => {
          if (res.confirm) {
            // Ê®°ÊãüËÅîÁ≥ªÊìç‰Ωú
            uni.showToast({
              title: 'Â∑≤ÂèëÈÄÅÊ∂àÊÅØÁªôÁî®Êà∑',
              icon: 'success'
            })
          }
        }
      })
    }
  }
}
</script>

